import myButton from '../../components/my-components/RoundButton';
export default {
    props: {
        rights: {
            type: Object,
            default: () => ({
                status: '退款退货',
                addtion: []
            })
        }
    },
    components: {
        myButton
    },
    methods: { 
        clickBill(){
            this.$emit('click',{
                type:'rights',
                detail:{
                    val:this.rights
                }
            })
        },
        clickButton(val) {
            this.$emit('click',{
                type:'button',
                detail:{
                    val
                }
            })
        }
    },
}
